pre[class*=language-] {
    color:$color-text;
    font-weight:$font-weight-normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
    color:slategray;
}

.token.punctuation {
    color:$color-text;
}

.namespace {
    opacity:0.7;
}

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
    color:#990055;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
    color:#669900;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
    background:hsla(0, 0%, 100%, 0.5);
    color:#9a6e3a;
}

.token.atrule,
.token.attr-value,
.token.keyword {
    color:#0077aa;
}

.token.function,
.token.class-name {
    color:#dd4a68;
}

.token.regex,
.token.important,
.token.variable {
    color:#ee9900;
}

.token.important,
.token.bold {
    font-weight:bold;
}

.token.italic {
    font-style:italic;
}

.token.entity {
    cursor:help;
}
